export default class WaypointsModel {
    #service;
    #waypoints = [];
    constructor(service) {
        this.#service = service;
        this.#waypoints = this.#service.points;
    }
    get waypoints() {
        return this.#waypoints;
    }
    getById(id) {
        return this.#waypoints.find((waypoint) => waypoint.id === id);
    }
}
