import { checkMatch } from '../../utils/utils';
const getTemplate = (currentName, name) => `<option value="${currentName}"${checkMatch(name, currentName, 'checked')}></option>`;
const createDestinationsListTemplate = (name, allDestinationsNames) => allDestinationsNames.map((currentName) => getTemplate(currentName, name)).join('');
export { createDestinationsListTemplate };
