import { capitalLetter, checkMatch } from '../../utils/utils';
import { POINTS_TYPES } from '../../const';
const getTemplate = (waypointsType, type) => `<div class="event__type-item"><input id="event-type-${waypointsType}-1" class="event__type-input  visually-hidden" type="radio" name="event-type" value="${waypointsType}" ${checkMatch(type, waypointsType, 'checked')}>
  <label class="event__type-label  event__type-label--${waypointsType}" for="event-type-${waypointsType}-1">${capitalLetter(waypointsType)}</label>
</div>`;
const createWaypointsTypesListTemplate = (type) => POINTS_TYPES.map((waypointsType) => getTemplate(waypointsType, type)).join('');
export { createWaypointsTypesListTemplate };
