import View from '../../framework/view/view';
import { WaypointsEmptyMessages } from '../../const';
const getTemplate = (filter) => `<p class="trip-events__msg">${WaypointsEmptyMessages[filter]}</p>`;
export default class ListEmptyView extends View {
    #filter;
    constructor(filter) {
        super();
        this.#filter = filter;
    }
    get template() {
        return getTemplate(this.#filter);
    }
}
